header {
  background-color: #f8f8f8;
  height: 30px;
  width: 100%;
  line-height: 28px;
  color: #717171; }
  header .header-left {
    float: left;
    overflow: hidden; }
    header .header-left strong {
      color: #e62318; }
    header .header-left i {
      color: #e62318;
      padding: 2px;
      font-size: 12px; }
    header .header-left a {
      padding: 2px; }
    header .header-left .username {
      margin-left: 6px;
      color: black;
      display: none; }
      header .header-left .username:hover {
        color: #e62318; }
    header .header-left .user_out {
      display: none; }
    header .header-left .login {
      margin: 0 7px; }
  header .header-right {
    float: right; }
    header .header-right i {
      display: inline-block;
      width: 7px;
      height: 4px;
      background: url(../../dist/images/nav.png) no-repeat;
      position: relative;
      top: -3px;
      -webkit-transition: .2s ease-in-out;
      -o-transition: .2s ease-in-out;
      transition: .2s ease-in-out; }
    header .header-right li {
      float: left;
      line-height: 28px;
      position: relative;
      z-index: 50; }
    header .header-right li:last-of-type {
