      overflow: hidden; }
    header .header-right li:hover:hover > a {
      color: #e62318; }
    header .header-right li:hover i {
      -webkit-transform: rotate(-180deg);
          -ms-transform: rotate(-180deg);
              transform: rotate(-180deg); }
    header .header-right li:hover .sub-menu {
      border-color: #eaeaea;
      display: block; }
    header .header-right li:hover .top-text {
      border-color: #eaeaea;
      background-color: #fff; }
      header .header-right li:hover .top-text a {
        color: #e62318; }
    header .header-right li:hover .sub-particulars {
      display: block; }
    header .header-right li:hover .sub-erweima {
      display: block; }
    header .header-right li:hover .sub-help {
      display: block; }
    header .header-right .top-text {
      padding: 0 5px;
      border: 1px solid transparent;
      border-bottom: 0; }
    header .header-right .my-center .top-text {
      width: 71px; }
    header .header-right .my-center .sub-menu {
      width: 71px;
      padding: 0 5px;
      border: 1px solid transparent;
      border-top: 0;
      background-color: white;
      display: none;
      text-align: left;
      position: absolute; }
      header .header-right .my-center .sub-menu a {
        display: block; }
    header .header-right .shopping-trolley {
      width: 92px; }
      header .header-right .shopping-trolley em {
        display: inline-block;
        width: 17px;
        height: 14px;
        background: url(../../dist/images/nav.png) no-repeat;
        background-position: 0 -53px;
        position: relative;
        top: 1px; }
      header .header-right .shopping-trolley .sub-particulars {
        width: 245px;
        background: #fff;
        border: 1px solid #eaeaea;
        border-top: 0;
        display: none;
        position: absolute; }
        header .header-right .shopping-trolley .sub-particulars .shopping-null {
          padding: 30px 15px; }
    header .header-right .attention {
      width: 106px; }
      header .header-right .attention em {
        display: inline-block;
        width: 17px;
        height: 14px;
        background: url(../../dist/images/nav.png) no-repeat;
        background-position: 0 -10px;
        position: relative;
        top: 2px; }
      header .header-right .attention .sub-erweima {
        text-align: center;
        display: none;
        position: absolute; }
        header .header-right .attention .sub-erweima .erweima {
          width: 132px;
          padding: 14px 22px;
          border: 1px solid #eaeaea;
          border-top: 0;
          background-color: white; }
    header .header-right .help .top-text {
      width: 59px; }
    header .header-right .help .sub-help {
      width: 59px;
      padding: 0 5px;
      border: 1px solid #eaeaea;
      border-top: 0;
      background-color: white;
      display: none;
      text-align: left;
      position: absolute; }
      header .header-right .help .sub-help a {
        display: block; }
    header .header-right .tel-scroll {
      margin-left: 10px;
      position: relative; }
      header .header-right .tel-scroll strong {
        color: #e62318;
        margin-left: 2px; }

.footer-icon {
  margin-top: 35px;
  padding: 35px 0;
  border-bottom: 1px solid #ededed;
  background: #f8f8f8; }
  .footer-icon a {
    width: 150px;
    height: 80px;
    float: left;
    text-align: center; }
    .footer-icon a i {
      display: block;
      width: 50px;
      height: 50px;
      margin: 0 auto;
      background: url(/dist/images/footerIcon.png); }
    .footer-icon a:nth-of-type(2) > i {
      background-position: -50px 0; }
    .footer-icon a:nth-of-type(3) > i {
      background-position: -100px 0; }
    .footer-icon a:nth-of-type(4) > i {
      background-position: -150px 0; }
    .footer-icon a:nth-of-type(5) > i {
      background-position: 0 -50px; }
    .footer-icon a:nth-of-type(6) > i {
      background-position: -50px -50px; }
    .footer-icon a:nth-of-type(7) > i {
      background-position: -100px -50px; }
    .footer-icon a:nth-of-type(8) > i {
      background-position: -150px -50px; }
    .footer-icon a span {
      display: block;
      width: 100%;
      margin-top: 10px;
      font-size: 14px;
      color: #333; }

